/**
 * DatePickerV2.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import document from 'global/document';
import window from 'global/window';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import ReactDOM from 'react-dom';
import { Field, getFormValues } from 'redux-form';
import PropTypes from 'prop-types';
import moment from 'moment';

import Arrow from 'assets/images/forms/arrow.svg';
import DateIn from 'assets/images/forms/dateIn.svg';
import DateOut from 'assets/images/forms/dateOut.svg';

import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';
import { isDateEqual, storeStringToDate } from '../../../lib/dateFunctions';

import '../DropDown/DropDown.scss';
import './DatePickerV2.scss';

/**
 * Constants
 */
// Placeholder string when no input
const placeholderStringDefault = 'dd/mm/yyy';
// Month text for display
const monthTextMapping = [
  'January',
  'Feburary',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

// Height for medium dropdown
const mediumHeight = 60;
// Height for big dropdown
const bigHeight = 72;
// Default year range if not user defined
const deafultYearRange = 100;


/**
 * Custom Form Dropdown class
 */
class DatePickerV2 extends Component {
  /**
   * Lifecycle Method - getDerivedStateFromProps
   *
   * Used to set sibling date range values for
   * selected start and end date
   *
   * @param {object} nextProps Component props
   */
  static getDerivedStateFromProps(nextProps, prevState) {
    const result = {};

    // Update selected Start End date state
    function updateStartEndDate(dateName, date) {
      const dateReformed = storeStringToDate(date);
      if (dateReformed !== -1) {
        result[dateName] = dateReformed;
      }
    }

    // Only valid for date range picker
    const { isRange, endDate } = nextProps;

    if (isRange) {
      const {
        incomingStartDate,
        incomingEndDate,
      } = nextProps;
      updateStartEndDate('selectedStartDate', incomingStartDate);
      updateStartEndDate('selectedEndDate', incomingEndDate);
    }

    // Update end date from date range picker
    if (endDate && !isDateEqual(endDate, prevState.dateRangeEnd)) {
      result.dateRangeEnd = endDate;
    }

    return result === {} ? null : result;
  }

  /**
   * Constructor
   *
   * @param {object} props Component props
   */
  constructor(props) {
    super(props);

    // size - Size of this control
    // startDate - Start range of date
    // endDate - End range of date
    // currDate - Day to set current view in calender (Default to today)
    const {
      size,
      startDate,
      endDate,
      currDate,
      placeholderString,
    } = this.props;

    // Get the control height
    const height = size === 'big' ? bigHeight : mediumHeight;

    // Get current date
    const date = currDate || new Date();
    const year = date.getFullYear();
    const month = date.getMonth();
    // Get first and last day of the month to display
    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);
    // Get the calender range
    const dateRangeStart = startDate != null ? startDate : new Date(year - deafultYearRange, month);
    const dateRangeEnd = endDate != null ? endDate : new Date(year + deafultYearRange, month);

    // Get default placeholder string
    const placeHolder = placeholderString != null ? placeholderString : placeholderStringDefault;

    // isOpened - Whether dropdown is opened
    // isError - Whether there is input error
    // selectedString - The selected date string
    // height - Control height
    // currDate - Current selected date in view
    // currFirstDay - Current first day of the month in view
    // currLastDay - Current last day of the month in view
    // monthDropdownOpen - Whether month selector is opened
    // yearDropdownOpen - Whether year selector is opened
    // selectedStartDate - Selected start date(If single select also use this)
    // selectedEndDate - Selected end date for date range picker
    // dateRangeStart - The actual calender start range
    // dateRangeEnd - The actual calender end range
    this.state = {
      isOpened: false,
      selectedString: placeHolder,
      height,
      currDate: date,
      currFirstDay: firstDay,
      currLastDay: lastDay,
      selectedStartDate: null,
      selectedEndDate: null,
      dateRangeStart,
      dateRangeEnd,
      placeHolder,
    };

    this.handleSelectClick = this.handleSelectClick.bind(this);
    this.handleClickOutside = this.handleClickOutside.bind(this);
    this.handleOnInputChange = this.handleOnInputChange.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.handleExit = this.handleExit.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
    this.handleFocus = this.handleFocus.bind(this);

    this.setSelectedDate = this.setSelectedDate.bind(this);
    this.setNewDate = this.setNewDate.bind(this);
    this.setCalender = this.setCalender.bind(this);
    this.onMovement = this.onMovement.bind(this);

    this.isFromFocus = false;
  }

  /**
   * Lifecycle Method - componentDidMount
   */
  componentDidMount() {
    // Add Handler to handle click outside of screen
    document.addEventListener('mousedown', this.handleClickOutside);
    window.addEventListener('scroll', this.onMovement);
    window.addEventListener('resize', this.onMovement);

    const {
      name,
      formStore,
      dateInner,
    } = this.props;

    // Update prefill dates
    if (formStore && formStore[name]) {
      this.setSelectedDate(storeStringToDate(formStore[name]));
    }

    this.setState({
      dateInnerDOM: ReactDOM.findDOMNode(dateInner),
      datePickerDOM: ReactDOM.findDOMNode(this.wrapperRef),
    });

    this.onMovement();
  }

  /**
   * Lifecycle Method - componentDidUpdate
   *
   * Used to update dropdown position
   * of month and year selector
   */
  componentDidUpdate(prevProps, prevState) {
    const {
      currDate,
      selectedStartDate,
      selectedEndDate,
      dateRangeEnd,
    } = this.state;

    const { isStart } = this.props;

    // If end date is shorten because of start date and
    // end date is beyond range, shift back date
    if (!isDateEqual(prevState.dateRangeEnd, dateRangeEnd)) {
      if (dateRangeEnd < selectedEndDate) {
        this.setSelectedDate(dateRangeEnd);
      }
    }

    // Set month of end date to be not lesser than start date
    if (!isStart && selectedStartDate && currDate && currDate < selectedStartDate) {
      this.setNewDate(selectedStartDate);
    }
  }

  /**
   * Lifecycle Method - componentWillUnmount
   */
  componentWillUnmount() {
    const { dateInner } = this.props;
    // Remove Handler to handle click outside of screen
    document.removeEventListener('mousedown', this.handleClickOutside);
    window.removeEventListener('scroll', this.onMovement);
    window.removeEventListener('resize', this.onMovement);

    if (dateInner) {
      dateInner.toggleDateInner(false);
    }
  }

  onMovement() {
    const { dateInnerDOM, datePickerDOM, isOpened } = this.state;
    const { dateInner } = this.props;

    if (isOpened && dateInnerDOM && datePickerDOM) {
      const isOverHeight = datePickerDOM.getBoundingClientRect().bottom + 540 > window.outerHeight;
      dateInner.setArrowUp(!isOverHeight);
      const top = isOverHeight ? datePickerDOM.getBoundingClientRect().top - 450 : datePickerDOM.getBoundingClientRect().bottom + 10;
      dateInnerDOM.style.left = `${datePickerDOM.getBoundingClientRect().left}px`;
      dateInnerDOM.style.top = `${top}px`;
    }
  }

  /**
   * setNewDate
   *
   * Set new current date and
   * start and end of calender
   *
   * @param {date} date
   */
  setNewDate(date, extraState = {}, selectedCallback) {
    const { isOpened } = this.state;
    const { dateInner } = this.props;
    const newYear = date.getFullYear();
    const newMonth = date.getMonth();

    // Get first and last day of visible month
    const firstDay = new Date(newYear, newMonth, 1);
    const lastDay = new Date(newYear, newMonth + 1, 0);

    // Set new calender values
    this.setState({
      currDate: date,
      currFirstDay: firstDay,
      currLastDay: lastDay,
      ...extraState,
    }, () => {
      if (selectedCallback) {
        selectedCallback({
          currDate: date,
          currFirstDay: firstDay,
          currLastDay: lastDay,
          selectedStartDate: extraState.selectedStartDate,
          selectedEndDate: extraState.selectedEndDate,
        });
      } else if (isOpened) {
        dateInner.setNewState({
          currDate: date,
          currFirstDay: firstDay,
          currLastDay: lastDay,
        });
      }
    });
  }

  /**
   * setCalender
   *
   * Set visible calender
   *
   * @param {number} month
   * @param {number} year
   */
  setCalender(month, year) {
    const {
      dateRangeStart,
      dateRangeEnd,
    } = this.state;

    let date = new Date(year, month);

    // Cap date range
    if (date < dateRangeStart) {
      date = dateRangeStart;
    } else if (date > dateRangeEnd) {
      date = dateRangeEnd;
    }

    this.setNewDate(date);
    this.wrapperRef.focus();
  }

  /**
   * setSelectedDate
   *
   * Updated selected date to store
   *
   * @param {date} date
   */
  setSelectedDate(date, selectedCallback = () => {}) {
    const { selectedStartDate, selectedEndDate } = this.state;

    const {
      change,
      name,
      isStart,
      isRange,
      endRef,
      // dateInner,
    } = this.props;

    try {
      let storeString = null;
      let dateString = null;
      if (date) {
        // Get date values in string
        const currDay = (`0${date.getDate().toString()}`).slice(-2);
        const currMonth = (`0${(date.getMonth() + 1).toString()}`).slice(-2);
        const currYear = date.getFullYear().toString();

        // Get date in string
        dateString = `${currDay}/${currMonth}/${currYear}`;
        // String to store in store
        storeString = `${currDay} ${monthTextMapping[currMonth - 1].slice(0, 3)} ${currYear}`;
      }
      
      // Update the date in the store for the form
      change(name, storeString);

      let newSelectedStartDate = selectedStartDate;
      let newSelectedEndDate = selectedEndDate;

      // Set end and start to selected date
      // Default use selectedStartDate if not range
      if (isRange && !isStart) {
        newSelectedEndDate = date;
      } else {
        newSelectedStartDate = date;
        if (newSelectedEndDate < newSelectedStartDate) {
          newSelectedEndDate = newSelectedStartDate;
          
          if (endRef && endRef.wrappedInstance) {
            endRef.wrappedInstance.setSelectedDate(newSelectedStartDate);
          }
        }
      }

      // Set the new date
      this.setNewDate(date, {
        isOpened: false,
        selectedString: dateString,
        selectedEndDate: newSelectedEndDate,
        selectedStartDate: newSelectedStartDate,
      }, selectedCallback);

      // Close the dropdown and update the selectedString
      // Since is selection, there will be no error
      // this.setState({
      //   isOpened: false,
      //   selectedString: dateString,
      //   selectedEndDate: newSelectedEndDate,
      //   selectedStartDate: newSelectedStartDate,
      // });

      this.isFromFocus = false;
    } catch (error) {
      // eslint-disable-next-line no-empty
    }
  }

  /**
   * End selection if click on outside
   *
   * @param {object} event
   */
  handleClickOutside(event) {
    const { isOpened, dateInnerDOM } = this.state;
    const { isTargetWithinDateRangePicker, dateInner } = this.props;

    // If clicked outside of daterpicekr Inner or wrapper, go to exit
    if (
      isOpened &&
      this.wrapperRef &&
      !this.wrapperRef.contains(event.target) &&
      dateInnerDOM &&
      !dateInnerDOM.contains(event.target)
    ) {
      this.setState({
        isOpened: false,
      });

      // If outside of any datepicker controls
      if (!isTargetWithinDateRangePicker || (isTargetWithinDateRangePicker && !isTargetWithinDateRangePicker(event.target))) {
        dateInner.toggleDateInner(false);
      }

      this.handleExit();

      this.isFromFocus = false;
      // this.handleExit();
      this.doNotBlur = false;
      return;
    }

    this.doNotBlur = true;
  }

  /**
   * handleKeyPress
   *
   * Handle on enter key
   *
   * @param {object} target
   */
  handleKeyPress(target) {
    const { dateInner } = this.props;

    // If pressed on enter, go to exit
    if (target.charCode === 13) {
      target.preventDefault();
      this.handleExit();
      dateInner.toggleDateInner(false);
    }
  }

  /**
   * handleSelectClick
   *
   * Handle toggling of datepicker
   */
  handleSelectClick() {
    const {
      isOpened,
      placeHolder,
      currDate,
      currFirstDay,
      currLastDay,
      selectedStartDate,
      selectedEndDate,
      dateRangeStart,
      dateRangeEnd,
    } = this.state;

    const {
      change,
      name,
      dateInner,
      isRange,
      isStart,
      formStore,
    } = this.props;

    let selectedStart = selectedStartDate || (formStore && storeStringToDate(formStore.start_date));
    let selectedEnd = selectedEndDate || (formStore && storeStringToDate(formStore.end_date));
    selectedStart = selectedStart === -1 ? null : selectedStart;
    selectedEnd = selectedEnd === -1 ? null : selectedEnd;

    const selectedCallback = (extraState = {}) => {
      dateInner.toggleDateInner(!isOpened, {
        setNewDate: this.setNewDate,
        setSelectedDate: this.setSelectedDate,
        currDate,
        currFirstDay,
        currLastDay,
        selectedStartDate: selectedStart,
        selectedEndDate: selectedEnd,
        dateRangeStart,
        dateRangeEnd,
        isRange,
        isStart,
        ...extraState,
      });

      // ]if (!this.isFromFocus) {
      this.setState({
        isOpened: !isOpened,
      }, () => {
        if (!isOpened) {
          this.onMovement();
        }
      });

      const { selectedString } = this.state;
      // Open if not opened and set the input string
      // back to date form
      if (!isOpened) {
        if (selectedString !== placeHolder) {
          change(name, selectedString);
        }
      } else {
        this.handleExit();
        if (this.inputField) {
          const input = this.inputField.getRenderedComponent();
          if (input) {
            setTimeout(() => input.blur(), 2);
          }
        }
      }
      // }

      this.isFromFocus = false;
    };

    if (isStart && selectedStart) {
      this.setSelectedDate(selectedStart, selectedCallback);
    } else if (selectedEnd) {
      this.setSelectedDate(selectedEnd, selectedCallback);
    } else {
      selectedCallback();
    }

    // setTimeout(() => {
    //   dateInner.toggleDateInner(!isOpened, {
    //     setNewDate: this.setNewDate,
    //     setSelectedDate: this.setSelectedDate,
    //     currDate,
    //     currFirstDay,
    //     currLastDay,
    //     selectedStartDate: selectedStart,
    //     selectedEndDate: selectedEnd,
    //     dateRangeStart,
    //     dateRangeEnd,
    //     isRange,
    //     isStart,
    //   });

    //   // ]if (!this.isFromFocus) {
    //   this.setState({
    //     isOpened: !isOpened,
    //   }, () => {
    //     if (!isOpened) {
    //       this.onMovement();
    //     }
    //   });

    //   const { selectedString } = this.state;
    //   // Open if not opened and set the input string
    //   // back to date form
    //   if (!isOpened) {
    //     if (selectedString !== placeHolder) {
    //       change(name, selectedString);
    //     }
    //   } else {
    //     this.handleExit();
    //     if (this.inputField) {
    //       const input = this.inputField.getRenderedComponent();
    //       if (input) {
    //         setTimeout(() => input.blur(), 2);
    //       }
    //     }
    //   }
    //   // }

    //   this.isFromFocus = false;
    // }, 10);
  }

  /**
   * Handles on input changes
   *
   * @param {object} event
   */
  async handleOnInputChange(event) {
    // Wait for event to finish
    await event;
    // Get current input value
    const value = event.target.defaultValue;

    // Set new list items, height of dropdown list,
    // Force open list on type, and set selected string to value
    this.setState({
      selectedString: value,
    });
  }

  handleBlur(event) {
    const isChildren = this.wrapperRef.contains(event.relatedTarget);
    if (!isChildren && !this.doNotBlur) {
      // Hackish, but to make sure this comes after setNewDate
      setTimeout(() => {
        this.handleExit();
        this.isFromFocus = false;
      }, 100);
    }

    this.doNotBlur = false;
  }

  handleFocus(event) {
    const isChildren = this.wrapperRef.contains(event.relatedTarget);
    if (event.relatedTarget && !isChildren && !this.isClicked) {
      this.handleSelectClick();
      this.isFromFocus = true;
    }

    this.isClicked = false;
  }
  /**
   * handleExit
   *
   * Handle exit selection of entered input
   */
  handleExit() {
    const {
      selectedString,
      dateRangeEnd,
      dateRangeStart,
      selectedStartDate,
    } = this.state;

    const {
      change,
      name,
      isRange,
      isStart,
    } = this.props;

    // Check if input string is valid date
    let isError = !moment(selectedString, 'DD/MM/YYYY', true).isValid();
    // The date in date form
    let selectedDate = null;

    // Check if date is within date range
    if (!isError) {
      const parts = selectedString.split('/');
      selectedDate = new Date(parts[2], parts[1] - 1, parts[0]);
      // isError = isStart && selectedEndDate ? selectedDate > selectedEndDate : isError;
      isError = !isStart && selectedStartDate ? selectedStartDate > selectedDate : isError;
      isError = selectedDate < dateRangeStart || dateRangeEnd < selectedDate ? true : isError;
    }

    // Close the dropdown and set any errors
    this.setState({
      isOpened: false,
    });

    // Set null to selected start or end date if error
    if (isError) {
      if (isRange && !isStart) {
        this.setState({
          selectedEndDate: null,
        });
      } else {
        this.setState({
          selectedStartDate: null,
        });
      }

      // Null strings and store value
      this.setState({
        selectedString: '',
      });
      change(name, null);

      return;
    }

    this.setSelectedDate(selectedDate);
  }

  /**
   * Render component
   */
  render() {
    const {
      isOpened,
      height,
      placeHolder,
    } = this.state;

    if (height) {
      const {
        name,
        zIndex,
        maxWidth,
        isRange,
        isStart,
        desc,
        extraInfo,
        errorString,
        formStore,
        validateForm,
      } = this.props;

      const mainStyle = {
        height,
        zIndex,
        maxWidth,
      };

      const dropDownOpenStyle = isOpened ? 'dropdown-container-opened' : 'dropdown-container-closed';
      const isErrorStyle = errorString ? 'error' : '';
      const rangeIcon = isStart ? DateIn : DateOut;
      const rangIconStyle = isStart ? 'date-img-in' : 'date-img-out';

      const refValue = formStore ? formStore[name] : null;

      return (
        <FormInputWrapper
          desc={desc}
          ref={node => (this.formParent = node)}
          extraInfo={extraInfo}
          errorString={errorString}
          refValue={refValue}
          validateForm={validateForm}
        >
          <div
            className="dropdown datepicker"
            style={mainStyle}
            ref={node => (this.wrapperRef = node)}
            onBlur={event => this.handleBlur(event)}
            onFocus={event => this.handleFocus(event)}
          >
            <div
              className={`dropdown-container 
                          ${dropDownOpenStyle} 
                          ${isErrorStyle}
                        `}
              style={{ height }}
              onKeyUp={() => {}}
              role="button"
              tabIndex={-1}
            >
              <div
                className="selected-container align-items-center"
                style={{ height }}
              >
                {
                  isRange ?
                    <img
                      className={`date-img ${rangIconStyle}`}
                      src={rangeIcon}
                      alt="Date Range Icon"
                    />
                  :
                    null
                }
                <div className="selected-text text-style">
                  <Field
                    name={name}
                    ref={node => this.inputField = node}
                    withRef
                    component="input"
                    type="text"
                    placeholder={placeHolder}
                    autoComplete="off"
                    onChange={event => this.handleOnInputChange(event)}
                    onKeyPress={this.handleKeyPress}
                    onMouseDown={(e) => {
                      if (window.innerWidth <= 576) {
                        e.preventDefault();
                      }
                      this.isClicked = true;
                      this.handleSelectClick();
                    }}
                    tabIndex={0}
                  />
                </div>
                {
                  isRange ?
                    null
                  :
                    <div
                      role="button"
                      className="selected-arrow"
                      onMouseDown={() => { this.isClicked = true; this.handleSelectClick(); }}
                      tabIndex={-1}
                    >
                      <img
                        src={Arrow}
                        alt="arrow.svg"
                        className={`selected-arrow-style ${isOpened ? 'angle-up' : 'angle-down'}`}
                      />
                    </div>
                }
              </div>
            </div>
          </div>
        </FormInputWrapper>
      );
    }

    return null;
  }
}

DatePickerV2.defaultProps = {
  maxWidth: '100%',
  isStart: true,
  isRange: false,
  startDate: null,
  endDate: null,
  currDate: null,
  placeholderString: placeholderStringDefault,
  desc: null,
  extraInfo: null,
  errorString: null,
  formStore: null,
  validateForm: null,
  isTargetWithinDateRangePicker: null,
  endRef: null,
};

DatePickerV2.propTypes = {
  formStore: PropTypes.object,
  maxWidth: PropTypes.string,
  size: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  zIndex: PropTypes.number.isRequired,
  change: PropTypes.func.isRequired,
  isStart: PropTypes.bool,
  isRange: PropTypes.bool,
  startDate: PropTypes.instanceOf(Date),
  endDate: PropTypes.instanceOf(Date),
  currDate: PropTypes.instanceOf(Date),
  placeholderString: PropTypes.string,
  desc: PropTypes.string,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  validateForm: PropTypes.func,
  dateInner: PropTypes.object.isRequired,
  isTargetWithinDateRangePicker: PropTypes.func,
  endRef: PropTypes.object,
};

const mapStateToProps = (state, ownProps) => ({
  formStore: (getFormValues(ownProps.form)(state)),
  dateInner: state.datepicker.datePickerInner,
});

export default connect(mapStateToProps, null, null, { withRef: true })(DatePickerV2);
